.gpt3_navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.gpt3_navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.gpt3_navbar-links_logo {
    margin-right: 2rem;
}

.gpt3_navbar-links_logo img {
    width: 120px;
    height: 40px;
    justify-content: center ;
}

.gpt3_navbar-links_container{
    display: flex;
    flex-direction: row;

}

.gpt3_navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
}
.gpt3_navbar-sign p{
    padding: 1rem;
}

.gpt3_navbar-links_container p,
.gpt3_navbar-menu_container-links-sign p,
.gpt3_navbar-sign,
.gpt3_navbar-menu-container-links p,
.gpt3_navbar-menu_container p {
    color: #ffff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    
    margin: 0 1rem;
    cursor: pointer;
}

.gpt3_navbar-sign button,
.gpt3_navbar-menu_container-links-sign button,
.gpt3_navbar-menu_container button{
    padding: 0.5rem 1rem;
    color: #ffff;
    background: #FF4820;
    font-family: var(--font-family);
    line-height: 25px;
    font-size: 18px;  
    left: 1153px;
    top: 55px;
    outline: none;
    cursor: pointer;

    border-radius: 5px;
}

.gpt3_navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt3_navbar-menu svg{
    cursor: pointer;
}

.gpt3_navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--font-family);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 2);
}

.gpt3_navbar-menu_container p{
    margin: 1rem 0;
}

.gpt3_navbar-menu_container-links-sign {
    display: none;
}


@media  screen and (max-width: 1050px) {
    .gpt3_navbar-links_container{
        display: none;
    }
    .gpt3_navbar-menu{
        display: flex;
    }
}

@media  screen and (max-width: 700px) {
    .gpt3_navbar{
        padding: 2rem 4rem;
    }
}
@media  screen and (max-width: 700px) {
    .gpt3_navbar{
        padding: 2rem;
    }

    .gpt3_navbar-sign{
        display: none;
    }

    .gpt3_navbar-menu_container{
        top: 20px
    }

    .gpt3_navbar-menu_container-links-sign{
        display: block;
    }
}
